* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: var(--tg-theme-text-color, white);
  background: var(--tg-theme-bg-color, black);
  font-family: Quicksand, sans-serif;
  display: flex;
  height: var(--tg-viewport-height);
  align-items: center;
  justify-content: center;
}

button {
  font-family: inherit;
  color: inherit;
}